:root {
  --cards-hybrid-gutter-y: 1.5rem;
  --cards-hybrid-gutter-x: 2rem;
  --cards-hybrid-min: 22rem;
}
@media (max-width: 768px) {
  :root {
    --cards-hybrid-gutter-y: 1rem;
    --cards-hybrid-gutter-x: 1rem;
  }
}
@media (max-width: 480px) {
  :root {
    --cards-hybrid-min: 15rem;
  }
}
/* Organize cards in a grid instead of BootStrap */
.grid-cards-ct {
  display: grid;
  grid-gap: var(--cards-hybrid-gutter-y) var(--cards-hybrid-gutter-x);
  grid-template-columns: repeat(auto-fit, minmax(var(--cards-hybrid-min), 1fr));
  padding: var(--cards-hybrid-gutter-y) var(--cards-hybrid-gutter-x);
}
.grid-cards-ct > div {
  /* No funky negative margins are needed for grid */
  margin: 0;
}
@media screen and (min-width: 60rem) {
  /* Make a single card no longer than half the width */
  .grid-cards-ct > div:first-child:last-child {
    /* Remove 1/2 grid-gap to keep width the same */
    max-width: calc(50% - 10px);
  }
}
/* PatternFly overrides for hybrid style */
.cards-ct-hybrid .card-pf {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 0;
  max-width: calc(100vw - var(--cards-hybrid-gutter-x));
}
.cards-ct-hybrid .card-pf-title,
.cards-ct-hybrid .card-pf-body {
  margin: 0;
}
.cards-ct-hybrid .card-pf-title,
.cards-ct-hybrid .card-pf-title a {
  display: flex;
  align-items: center;
}
.cards-ct-hybrid .card-pf-title {
  flex: auto;
  padding: 1rem 0 1rem 1rem;
  font-size: var(--pf-global--FontSize--md);
}
.cards-ct-hybrid .card-pf-title a {
  color: #0088ce !important;
  margin: -20px;
  padding: 20px;
}
.cards-ct-hybrid .card-pf-title a:hover {
  text-decoration: none;
}
.cards-ct-hybrid .card-pf-title a:hover .card-pf-title-link {
  text-decoration: underline;
}
.cards-ct-hybrid .card-pf-title .pficon,
.cards-ct-hybrid .card-pf-title .fa {
  margin-right: 1rem;
}
.cards-ct-hybrid .card-pf-body {
  flex: 0 auto;
  padding: 10px 20px;
}
.cards-ct-hybrid .card-pf-aggregate-status-count {
  color: inherit;
  padding: 0 .5rem 0;
  font-size: var(--pf-global--FontSize--xl);
  font-weight: 600;
}
.cards-ct-hybrid .card-pf-aggregate-status-notifications {
  font-size: var(--pf-global--FontSize--md);
  font-weight: 600;
  margin: 0;
}
.cards-ct-hybrid .card-pf-aggregate-status-notifications .pficon,
.cards-ct-hybrid .card-pf-aggregate-status-notifications .fa {
  font-size: 1.25em;
  margin-right: 0.5rem;
  position: relative;
  /* Shove down from the top the (.25em difference) / 2 */
  top: 0.125em;
  vertical-align: top;
}
.cards-ct-hybrid .card-pf-aggregate-status-notification + .card-pf-aggregate-status-notification {
  margin-left: 1rem;
  padding-left: 1rem;
}
.panel .table {
  font-size: var(--font-size-s);
}
/* Panels don't draw borders between them */
.panel > .table > tbody:first-child td {
  border-top: 1px solid #dddddd;
}
/* Table headers should not generate a double border */
.panel .table thead tr th {
  border-bottom: none;
}
/* Fix panel heading alignment & mobile layout */
.panel-heading {
  align-items: center;
  background: #f5f5f5;
  display: flex;
  flex-wrap: wrap;
  /* (28px small size widget height) + (0.5rem * 2) */
  min-height: calc(28px + 1rem);
  padding: 0.5rem 0.5rem 0.5rem 1rem;
  position: relative;
  z-index: 100;
}
.panel-title {
  font: inherit;
  margin: 0;
  padding: 0;
}
.panel-title > a {
  color: var(--color-link);
  display: inline-block;
}
.panel-title > a:hover,
.panel-title > a:focus {
  color: var(--alert-info-text);
}
/* Allow children in the title to wrap */
.panel-title > h3,
.panel-title > a,
.panel-title > div,
.panel-title > span {
  flex-shrink: 1;
  word-break: break-all;
}
.panel-heading > :last-child:not(:first-child),
.panel-heading > .pull-right,
.panel-heading > .panel-heading-actions {
  flex: auto;
  text-align: right;
}
.panel-heading > .pull-right {
  /* HACK: Pull right shouldn't be used to change document order. */
  /* This CSS _also_ changes document order to replicate the float. */
  /* Solution: Fix document order when pull-right is used. */
  order: 100;
}
.panel-heading > .pull-right:first-child ~ :last-child {
  /* Undo :last-child above for cases where pull-right is used before */
  text-align: left;
}
@media screen and (max-width: 640px) {
  /* Remove _most_ of the gaps on the sides of small screens */
  /* to maximize space, but still keep the boxy panel look */
  /* (container-fluid & page-ct adds 20px, so we remove 1/2 of that) */
  .page-ct > .panel,
  .container-fluid > .panel,
  .col-md-12 > .panel {
    margin-left: -10px;
    margin-right: -10px;
  }
  .panel {
    /* Background fade */
    --hi-color: #d1d1d1;
    --hi-color2: var(--color-gray-2);
    --bg-color: var(--color-bg);
    --hi-width: 20px;
    --hi-width3: calc(var(--hi-width) * 3);
    --transparent: rgba(255, 255, 255, 0);
    /* WebKit needs white transparent */
    max-width: 100vw;
    overflow-x: auto;
    position: relative;
    background-image: linear-gradient(to left, var(--bg-color) var(--hi-width), var(--transparent) var(--hi-width3)), linear-gradient(to left, var(--hi-color) 1px, var(--transparent) 2px, var(--hi-color2) 4px, var(--bg-color) var(--hi-width)), linear-gradient(to right, var(--bg-color) var(--hi-width), var(--transparent) var(--hi-width3)), linear-gradient(to right, var(--hi-color) 1px, var(--transparent) 2px, var(--hi-color2) 4px, var(--bg-color) var(--hi-width));
    background-attachment: local, scroll, local, scroll;
    background-position: right, right, left, left;
    background-repeat: no-repeat;
    background-size: var(--hi-width3) 100%;
  }
  .panel > .panel-heading {
    /* WebKit still needs -webkit- prefix for sticky */
    position: -webkit-sticky;
    position: sticky;
    left: 0;
    top: 0;
  }
  .panel .table thead th {
    white-space: nowrap;
  }
  .panel .table:not(:hover):not(:focus):not(:active) {
